import React, { useState, useContext, useEffect } from "react";
import { StoreContext } from "providers/StoreProvider";
import styled from "styled-components";
import ShoppingCartElement from "components/organisms/ShoppingCartElement/ShoppingCartElement";
import ShoppingCartPopUp from "components/templates/ShoppingCartPopUp/ShoppingCartPopUp";
import PopUpWindow from "components/molecules/PopUpWindow/PopUpWindow";
import { v4 as uuidv4 } from "uuid";

const CartButton = styled.button`
  width: 50px;
  height: 35px;
  background-color: #ffd1dc;
  position: fixed;
  border: none;
  border-radius: 10px;
  right: 20px;
  top: 20px;
  box-shadow: -2px 4px 10px ${({ theme }) => theme.colors.darkGrey};
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ShoppingCart = () => {
  const { isShow, setIsShow } = useContext(StoreContext);
  const { popUp, setPopUp, showPopUp } = useContext(StoreContext);
  //console.log([popUp.show]);
  return (
    <>
      {popUp.show === true ? (
        [
          useEffect(() => {
            const timer = setTimeout(() => {
              setPopUp(!popUp.show);
            }, 1000);
            return () => clearTimeout(timer);
          }, []),
          <PopUpWindow type={popUp.type} />,
        ]
      ) : (
        <></>
      )}
      <CartButton onClick={() => setIsShow(!isShow)}>Cart</CartButton>

      {isShow ? (
        [
          <ShoppingCartPopUp />,
          <CartButton onClick={() => setIsShow(!isShow)}>Close</CartButton>,
        ]
      ) : (
        <></>
      )}
    </>
  );
};

export default ShoppingCart;
