import React, { useState, useContext, useEffect } from "react";
import { Wrapper } from "./Products.styles";
import Product from "components/organisms/Product/Product";
import { StoreContext } from "providers/StoreProvider";
import axios from "axios";

export const query = `
{
  allProducts(filter: {category: {matches: {pattern: "Hoodies"}}}) {
    id
    name
    model
    price
  }
}
      `;

const Hoodies = () => {
  const [hoodies, setHoodies] = useState([]);

  useEffect(() => {
    axios
      .post(
        "https://graphql.datocms.com/",
        {
          query,
        },
        {
          headers: {
            authorization: `Bearer ${process.env.REACT_APP_DATOCMS_KEY}`,
          },
        }
      )
      .then(({ data: { data } }) => {
        setHoodies(data.allProducts);
      })
      .catch((err) => {
        alert(err);
      });
  }, []);

  return (
    <>
      <Wrapper>
        {hoodies.map((item, num) => (
          <Product product={item} key={item.id} />
        ))}
      </Wrapper>
    </>
  );
};

export default Hoodies;
